@charset "UTF-8";

$padding: .7rem;

$small: "only screen and (min-width: 480px)";
$medium: "only screen and (min-width: 768px)";
$large: "only screen and (min-width: 1140px)";

@mixin tablet {
  @media #{$small} {
    @content;
  }
}

@mixin desktop {
  @media #{$medium} {
    @content;
  }
}

@mixin xldesktop {
  @media #{$large} {
    @content;
  }
}


@font-face {
  font-family:"ArialNarrowMTPro-Regular";
  src:url("./fonts/ArialNarrowMTPro-Regular.woff2") format("woff2"),url("./fonts/ArialNarrowMTPro-Regular.woff") format("woff"),url("./fonts/ArialNarrowMTPro-Regular.otf") format("opentype");
  font-style:normal;font-weight:400;
}
      
@font-face {
  font-family:"ArialNarrowMTPro-Bold";
  src:url("./fonts/ArialNarrowMTPro-Bold.woff2") format("woff2"),url("./fonts/ArialNarrowMTPro-Bold.woff") format("woff"),url("./fonts/ArialNarrowMTPro-Bold.otf") format("opentype");
  font-style:normal;font-weight:400;
}


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

@mixin goodtype {
  -webkit-text-rendering: optimizeLegibility;
  -moz-text-rendering: optimizeLegibility;
  -ms-text-rendering: optimizeLegibility;
  -o-text-rendering: optimizeLegibility;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-smoothing: grayscale;
  -webkit-font-feature-settings: "kern"1, "dlig"1, "ss01"1;
  font-feature-settings: "kern"1, "dlig"1, "ss01"1;
  text-size-adjust: 100%;
  text-decoration: none;
}

html,
body {
  @include goodtype;
  overflow: hidden;
  font-family: "ArialNarrowMTPro-Regular", serif;
  font-size: 15px;
  line-height: 1.36;
  text-transform: uppercase;
  background: black;
  color: white;
  padding: rem;
  @include desktop {
    font-size: 16px;
    line-height: 1.36;
  }
}

.small {
  font-size: .6rem;
}
.strong {
  font-family: "ArialNarrowMTPro-Bold", serif;
}
.mb05 {
  margin-bottom: .5rem;
}

a {
  text-decoration: none;
  color: white;
  width: 100%;
  &:hover {
    text-decoration: underline;
  }
  &:visited {
    color: white;
  }
}
.smiley {
  font-size: 1.2rem;
  line-height: 0;
}

#canvas {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  filter: blur(60px);
  z-index: 1;
}

.main-wrapper {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding: $padding;
  z-index: 10;
}
.meta-wrapper {
  width: 100%;
}

.footer {
  .footer-l {
    position: absolute;
    bottom: $padding;
    left: $padding;
  }
  .footer-r {
    position: absolute;
    bottom: 1rem;
    right: $padding;
  }
}